<template>
  <div style="width:100%;height:100%">
    <div class="index-left">
      <menu-common :activeIndex="activeIndex"></menu-common>
    </div>
    <div class="index-right">
      <index-nav-top></index-nav-top>
      <div class="index-right-section">
        <div class="section-content">
          <div class="section-content-top">
            <div class="common-marker fl">
            </div>
            <p class="fl">商品列表</p>
          </div>
          <div>
            <el-button class="common-btn" type="warning" size="mini" @click="addProduct">增加商品</el-button>
          </div>
          <el-row style="margin:10px 0">
            <el-col :span="24">
              <span class="fl common-search-text">
                商品编号：
              </span>
              <el-input v-model="postData.num" size="medium" class="common-small-length common-search-ipt fl" placeholder="请输入内容"></el-input>
              <span class="fl common-search-text">
                店铺：
              </span>
              <el-select v-model="value" placeholder="请选择" class="common-small-length common-search-ipt fl">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <span class="fl common-search-text">
                分类：
              </span>
              <el-select v-model="value" placeholder="请选择" class="common-small-length common-search-ipt fl">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <span class="fl common-search-text">
                标题：
              </span>
              <el-select v-model="value" placeholder="请选择" class="common-small-length common-search-ipt fl">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <el-button type="warning" size="small" style="margin-top: 3px;">查询</el-button>
            </el-col>
          </el-row>
          <el-table
            :data="tableData"
            height="550"
            border
            style="width: 100%">
            <el-table-column
              prop="date"
              label="商品编号"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="name"
              label="店铺"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="address"
              align="center"
              label="分类">
            </el-table-column>
            <el-table-column
              prop="date"
              label="标题"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="图片"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="状态"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="推荐状态"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="总库存"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="原价"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="最低价"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="商品数据"
              align="center"
              width="180">
            </el-table-column>
            <el-table-column
              prop="date"
              label="操作"
              align="center"
              width="180">
            </el-table-column>
          </el-table>
          <div class="common-pagination">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[50]"
              :page-size="100"
              layout="total, sizes, prev, pager, next, jumper"
              :total="400">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MenuCommon from '../../components/menu'
import IndexNavTop from '../../components/indexNavTop'
export default {
  components: {
    IndexNavTop,
    MenuCommon},
  name: 'ProductList',
  data () {
    return {
      activeIndex: '1-2',
      tableData: [],
      currentPage: 1,
      postData: {},
      options: []
    }
  },
  methods: {
    addProduct () {
      this.$router.push('/index')
    },
    handleSizeChange() {

    },
    handleCurrentChange () {}
  }
}
</script>
<style scoped>
.common-btn{
  margin: 10px 0px 20px;
}
</style>



